'usr strict';
import React, {Component, PropTypes} from 'react';
import Picker from 'react-mobile-picker';
import ReactDOM, {render} from 'react-dom';


import '../../src/less/example.less'

export default class NamePicker extends Component {
    constructor(props) {
        super(props);


        this.state = {
            isPickerShow: this.props.isPickerShow,
            valueGroups: {
                radius: '500',


            },
            optionGroups: {
                radius: ['100', '200', '300', '400', '500', '600', '700', '800', '900', '1000'],

            }
        };
    }

    componentWillMount() {

        if (this.props.radius == 'null') {

            this.props.handleChange(this.state.valueGroups.radius.replace('', ''));
        }
    }


    handleChange(name, value) {

        this.setState({
            valueGroups: {
                radius: value
            }
        });

        this.props.handleChange(value.replace('', ''));

    }


    componentWillReceiveProps(nextProps) {

        if (nextProps.isPickerShow) {
            this.setState({
                isPickerShow: nextProps.isPickerShow,
                valueGroups: {
                    radius: nextProps.radius + ''
                }
            })
        }

    }


    togglePicker(e) {

        this.setState({
            isPickerShow: false
        });
    };


    render() {

        const {optionGroups, valueGroups, isPickerShow} = this.state;

        const maskStyle = {
            display: isPickerShow ? 'block' : 'none'
        };

        //   const pickerModalClass = 'picker-modal${isPickerShow' ? 'picker-modal-toggle' : '';
        const pickerModalClass = `picker-modal${isPickerShow ? ' picker-modal-toggle' : ''}`;


        return (

            <div className="example-container">
                <div className="picker-modal-container">
                    <div className="picker-modal-mask" style={maskStyle}></div>
                    <div className={pickerModalClass}>
                        <header>
                            <div className="title"><a href="javascript:;" onClick={this.togglePicker.bind(this)}></a>
                            </div>
                            <a href="javascript:;" onClick={this.togglePicker.bind(this)}></a>
                        </header>
                        <Picker
                            optionGroups={optionGroups}
                            valueGroups={valueGroups}
                            onChange={this.handleChange.bind(this)}/>
                    </div>
                </div>
            </div>
        );
    }
}


